<template>
    <div class="lightcontent">
        <template v-for="(item, index) in lightdata.result">
            <li @click="goState(item)" class="lightcontent-item" :key="index">
                <div class="item-img">
                    <img :src="item.url" :alt="item.title">
                </div>
                <div class="item-text">
                    <p>{{item.title}}</p>
                </div>
            </li>
        </template>
    </div>
</template>
<style lang="less">
    .lightcontent{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        padding: 1rem 2rem;
        .lightcontent-item{
            border: 0.1rem solid #e5e5e5;
            margin: 1rem 0;
            border-bottom: 0;
            flex-basis: 48%;
            .item-img{
                padding: 1rem 0;
                img{
                    display: block;
                    margin: 0 auto;
                    max-width: 50%;
                }
            }
            .item-text{
                background: #e5e5e5;
                text-align: center;
                padding: 1rem 0;
            }
        }
    }
</style>
<script>
    export default {
        name: 'lightcontent',
        props: ['lightdata'],
        methods: {
            goState(item){
                this.$router.push({name:'lightAbout',params:{
                    title:item.title,
                    data:item
                }})
            }
        },
    }
</script>